import React from 'react';
import PropTypes from 'prop-types';

import AnimatedEllipsis from 'colby-react-animated-ellipsis';
import searchIcon from 'colby-svg/jsx/search-icon';

import defaultStyles from './defaults.module.scss';

const SearchInput = ({
  optionsComponents,
  optionsPlacement,
  id,
  placeholderText,
  searchTerm,
  handleSearchTermChange,
  fetching,
  styles,
}) =>
  (<div className={styles.container}>
    {optionsComponents && optionsPlacement === 'top'
      ? <div className={styles.optionsContainer}>{optionsComponents}</div>
      : null}
    <label htmlFor={id} className={styles.label}>
      <span className={styles['label-name']}>
        {placeholderText}
      </span>
      <input
        id={id}
        className={styles.input}
        placeholder={placeholderText}
        type="search"
        value={searchTerm}
        onChange={(event) => {
          handleSearchTermChange(event.target.value);
        }}
      />
      <span className={styles.feedback}>
        {fetching === true ? <AnimatedEllipsis /> : searchIcon}
      </span>
    </label>
    {optionsComponents && optionsPlacement === 'bottom'
      ? <div className={styles.optionsContainer}>{optionsComponents}</div>
      : null}
  </div>);

SearchInput.defaultProps = {
  fetching: false,
  id: Math.random().toString(36).substr(2, 5),
  optionsComponents: null,
  optionsPlacement: 'bottom',
  placeholderText: 'Search',
  styles: defaultStyles,
};

SearchInput.propTypes = {
  handleSearchTermChange: PropTypes.func.isRequired,
  searchTerm: PropTypes.string.isRequired,

  fetching: PropTypes.bool,
  id: PropTypes.string,
  optionsComponents: PropTypes.arrayOf(PropTypes.any),
  optionsPlacement: PropTypes.oneOf(['top', 'bottom']),
  placeholderText: PropTypes.string,
  styles: PropTypes.objectOf(PropTypes.any),
};

export default SearchInput;



